.home-img{
    background: linear-gradient( 
            3420deg
         , transparent 60%, white 90%);
    height: 501px;
    width: 100%;
    min-width: 1200px !important;
    position: absolute;
    top: 0;
}

.hover-zoom{
    transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -webkit-transition: all 0.1s ease-in-out;
    cursor: default;
}
.home-title{
    cursor: default;
}

.hover-zoom:hover{
    transform: scale(1.1) !important;
}

.insta-social{
    width: 50px;
    height: 50px;
    padding: 10px;
}

.social-media a{
    text-decoration: none;
    color: black
}
.social-media > a:hover{
    text-decoration: none;
}

.css-selector {
    background: linear-gradient(89deg, #990000, #ffcc00);
    background-size: 400% 400%;

    -webkit-animation: coolbuttons 30s ease infinite;
    -moz-animation: coolbuttons 30s ease infinite;
    animation: coolbuttons 30s ease infinite;
}

@-webkit-keyframes coolbuttons {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes coolbuttons {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes coolbuttons {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}